@charset "UTF-8";
.parachute {
  animation: xAxis 20s infinite cubic-bezier(0.02, 0.01, 0.21, 1); }
  .parachute_img {
    animation: mymove 5s infinite; }
    .parachute_img:before {
      content: "";
      display: block;
      width: 60px;
      height: 60px;
      animation: swing ease-in-out 1s infinite alternate;
      transform-origin: center -20px; }

.cloud-img-1 {
  animation: swingcloud ease-in-out 8s infinite alternate; }

.plane {
  width: 400px;
  height: 400px;
  position: absolute;
  top: -16px;
  left: -108px;
  animation: animateCloud 23s linear infinite; }

@keyframes yAxis {
  100% {
    animation-timing-function: cubic-bezier(0.02, 0.01, 0.21, 1);
    transform: translateY(-400px); } }

@keyframes xAxis {
  50% {
    animation-timing-function: cubic-bezier(0.2, 0.27, 0.07, 1.64);
    transform: translateX(200px); } }

@keyframes swing {
  0% {
    transform: rotate(50deg); }
  100% {
    transform: rotate(-5deg); } }

@keyframes swingcloud {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(3deg); } }

@keyframes mymove {
  from {
    top: 0px; }
  to {
    top: 600px; } }

@keyframes right {
  0% {
    left: -108px; }
  100% {
    right: calc(100% + 108px); } }

@keyframes move {
  from {
    left: 0px; }
  to {
    right: 600px; } }

.small-cloud {
  height: 20px;
  width: 20px;
  margin-top: 80px;
  position: absolute;
  z-index: -1;
  animation: animateCloud 60s linear infinite; }

.small-cloud-img {
  height: 300px;
  width: 450px;
  z-index: -1; }

.small-cloud-img-2 {
  z-index: -1;
  height: 100px;
  animation: animateCloud 65s linear infinite; }

.small-cloud-div {
  position: relative;
  z-index: -1; }

@keyframes animateCloud {
  0% {
    margin-left: -1000px; }
  100% {
    margin-left: 100%; } }

a {
  text-decoration: none;
  color: #232323;
  font-family: georgia, sans-serif;
  transition: color 0.3s ease; }

a:hover {
  color: black;
  transform: translateX(10px); }

#menuToggle {
  display: block;
  position: relative;
  top: 20px;
  left: 50px;
  z-index: 1;
  -webkit-user-select: none;
  user-select: none; }

#menuToggle input {
  display: block;
  opacity: 30;
  width: 40px;
  height: 32px;
  position: absolute;
  top: -7px;
  left: -5px;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
  -webkit-touch-callout: none; }

#menuToggle span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: white;
  border-radius: 3px;
  z-index: 1;
  transform-origin: 4px 0px;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease; }

#menuToggle span:first-child {
  transform-origin: 0% 0%; }

#menuToggle span:nth-last-child(2) {
  transform-origin: 0% 100%; }

#menuToggle input:checked ~ span {
  opacity: 1;
  transform: rotate(45deg) translate(-2px, -1px);
  background: white; }

#menuToggle input:checked ~ span:nth-last-child(3) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2); }

#menuToggle input:checked ~ span:nth-last-child(2) {
  transform: rotate(-45deg) translate(0, -1px); }

#menu {
  position: absolute;
  width: 300px;
  margin: -100px 0 0 -50px;
  padding: 50px;
  padding-top: 125px;
  background: #f0f0f000;
  list-style-type: none;
  -webkit-font-smoothing: antialiased;
  /* to stop flickering of text in safari */
  transform-origin: 0% 0%;
  transform: translate(-100%, 0);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1); }

#menu li {
  padding: 10px 0;
  font-size: 22px; }

/*
 * And let's slide it in from the left
 */
#menuToggle input:checked ~ ul {
  transform: none; }

